body,
html {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to right, #0f3057, #00587a, #008891);
}

h1 {
  font-family: "Noto Sans TC", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.projects{display:flex;flex-direction:column;align-items:center}.projects .project-title{color:whitesmoke;font-size:56px;margin-top:50px}.projects .navlink-container{display:flex;flex-wrap:wrap;margin:0 10px;padding:20px 0;justify-content:space-between}.projects .navlink-container .link{text-decoration:none;margin:0 10px;color:whitesmoke}.projects .navlink-container .link .project-buttons{font-size:20px;background-color:transparent;cursor:pointer;position:relative}.projects .navlink-container .link .project-buttons:hover{color:#3eecc1;transition:0.5s}.projects .navlink-container .link .project-buttons::after{content:"";position:absolute;left:0;bottom:0;width:100%;height:2px;background-color:currentColor;color:#3eecc1;transform:scaleX(0);transform-origin:right;transition:transform 250ms ease-in}.projects .navlink-container .link .project-buttons:hover::after{transform:scaleX(1);transform-origin:left;color:#3eecc1}.projects .navlink-container .link:hover,.projects .navlink-container .active{color:#3eecc1}@media (min-width: 470px){.navlink-container{width:400px}}

.multi-container,.container{margin:100px 0}.card{margin:40px auto;width:90%;max-width:400px;background-color:#e7e7de}.card .btn-container{display:flex;justify-content:space-between}.card .btn-container .custom-btn{margin:10px}@media (min-width: 649px){.custom-btn{width:40%}}@media (min-width: 768px){.multi-container{display:grid;width:100%;grid-template-columns:auto auto;margin:100px 0}.card{margin:40px auto}}

.about{display:flex;flex-direction:column;max-width:600px;margin:0 auto}.about .about-title{color:#e7e7de;text-align:center;font-size:48px;margin:50px 0;order:1}.about .intro{background-color:#3eecc1;text-align:center;margin:0 auto;width:90%;order:2}.about .intro p{font-size:28px;margin:20px 0;color:#0f3057;border-radius:5px}.about .about-container{width:90%;margin:0 auto;margin-top:50px;order:4}.about .about-container p{font-size:18px;color:#e7e7de}.about .about-container .about-links{display:flex;flex-direction:column;margin:50px auto}.about .about-container .about-links .about-resume{text-decoration:none;font-size:30px;font-weight:700;color:whitesmoke;display:inline-block;position:relative}.about .about-container .about-links .about-resume:hover{color:#3eecc1;transition:0.5s}.about .about-container .about-links .download-icon{color:#3eecc1;opacity:0;display:inline-block;position:absolute;left:45px;bottom:5px}.about .about-container .about-links .about-resume:hover .download-icon{opacity:1;left:130px;transition:left 0.5s ease-out, opacity 0.5s ease-out}.about .img-container{width:90%;margin:0 auto;margin-top:50px;order:3}.about .img-container img{width:100%;object-fit:contain;border:solid 2px whitesmoke;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}@media (min-width: 1024px){.about{max-width:100%;margin:100px 0;display:grid;grid-template-columns:1fr 500px;grid-template-areas:"title img" "subtitle img" "text img";grid-column-gap:10px}.about .about-title{order:1;grid-area:title;text-align:left;margin:10px 0;margin-left:25px}.about .intro{order:2;grid-area:subtitle;grid-column:1 /-1;text-align:left;margin:0;width:100%}.about .intro p{margin-left:25px}.about .about-container{order:3;grid-area:text;margin:0;margin-top:25px;margin-left:25px}.about .about-container p{font-size:18px}.about .img-container{order:4;grid-area:img;margin:0 auto}}

.header{display:flex;flex-direction:column;align-items:center}.header .header-logo{margin-top:20px;width:140px;object-fit:contain}.header .header-nav{display:flex;flex-direction:column;margin-top:20px}.header .header-nav .link{margin-top:15px;text-decoration:none;color:whitesmoke}.header .header-nav .link .header-buttons{font-size:32px;font-weight:700;background-color:transparent;cursor:pointer;position:relative}.header .header-nav .link .header-buttons:hover{color:#3eecc1;transition:0.5s}.header .header-nav .link .header-buttons::after{content:"";position:absolute;left:0;bottom:0;width:100%;height:2px;background-color:currentColor;color:#3eecc1;transform:scaleX(0);transform-origin:right;transition:transform 250ms ease-in}.header .header-nav .link .header-buttons:hover::after{transform:scaleX(1);transform-origin:left;color:#3eecc1}.header .header-nav .header-buttons:hover,.header .header-nav .active{color:#3eecc1}@media (min-width: 650px){.header{flex-direction:row;justify-content:space-between;height:140px}.header .header-logo{margin-top:0;width:120px;margin-left:30px}.header .header-nav{flex-direction:row}.header .header-nav .link{margin-top:0}.header .header-nav .link .header-buttons{padding:20px;margin-right:30px;font-size:24px}}

.home{display:flex;flex-direction:column;margin:100px 0;align-items:center}.home .avatar-img{width:250px;height:250px;margin:0 auto;display:block;border-radius:50%;border:3px solid whitesmoke}.home .home-title{background-color:#3eecc1;text-align:center;margin-top:100px;width:92%;max-width:690px}.home .home-title h1{font-size:60px;font-weight:800;color:#0f3057;padding:10px;margin-bottom:0}.home .introduction{padding:20px;margin:0 auto;margin-bottom:100px;max-width:730px}.home .introduction p{font-size:22px;color:whitesmoke;margin-top:20px}.home .introduction p .bold{font-weight:bold;text-decoration:none;color:whitesmoke}.home .skills-container{width:100%;margin:0 auto;text-align:center;background-color:#0f3057;border:2px solid #e7e7de}.home .skills-container .badge-title{color:#e7e7de;margin:40px 0;font-size:48px}.home .skills-container .badge-container{display:grid;width:100%;grid-template-columns:auto auto;grid-row-gap:10px;align-items:center;max-width:600px;margin:0 auto}.home .skills-container .badge-container .badges{width:100px;height:100px;background-color:whitesmoke;border-radius:50%;margin:20px auto;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.home .skills-container .badge-container .badges img{position:relative;top:12.5%;width:75px}@media (min-width: 768px){.avatar-img{width:300px;height:300px}}@media (min-width: 1024px){.home{max-width:100%;margin:100px 0;display:grid;grid-template-columns:1fr 500px;grid-template-areas:"img title" "img text" "badges badges";grid-column-gap:10px}.home .avatar-container{grid-area:img;z-index:10}.home .avatar-container .avatar-img{width:400px;height:400px}.home .home-title{grid-area:title;grid-column:1 /-1;width:100%;max-width:100%;text-align:left}.home .home-title h1{width:460px;margin-left:auto;margin-right:30px}.home .introduction{grid-area:text}.home .skills-container{grid-area:badges;margin-top:100px;padding-bottom:100px}}

* {
  margin: 0;
}

.app {
  max-width: 1080px;
  margin: 0 auto;
  height: 100%;
}

.btn-color {
  background-color: #00587a;
  color: #e7e7de;
}

